


.carousel {
	height:-webkit-fill-available;
	margin: 0;
	width: 100%;
}

.carousel-inner {
	height:-webkit-fill-available;
}

.carousel-item {
	height:-webkit-fill-available;
}

.carousel-item.active{
	height:-webkit-fill-available;
}

.carousel-caption {
    color: #000000;
}

.image-slide {
	height: 100%;
	width: 100%;
	transition: background-image .3s ease-in-out;
}

.slide-arrow {
	color: #14B6D4;
	cursor: pointer;
	font-size: 2rem;
	position: fixed;
	top: 50%;
}

.slide-arrow.right {
	right: 1rem;
}

.slide-arrow.left {
	left: 1rem;
}
